//对笔记的操作，根据用户点击的笔记本获取对应的笔记列表
import request from "../helpers/request";
import {friendlyDate} from "@/helpers/util";

const URL = {
  GET: '/notes/from/:notebookId',
  ADD: '/notes/to/:notebookId',
  UPDATE: '/notes/:noteId',
  DELETE: '/notes/:noteId'
}

export default {
  getAll({ notebookId }) {
    return new Promise((resolve, reject) => {
      request(URL.GET.replace(':notebookId', notebookId))
        .then(res => {
          //获取笔记数据前对时间进行处理，排序后的时间数组
          res.data = res.data.map(note => {
            note.createdAtFriendly = friendlyDate(note.createdAt)
            note.updatedAtFriendly = friendlyDate(note.updatedAt)
            return note
          }).sort((note1, note2) => {
            return note1.updatedAt < note2.updatedAt
          })
          resolve(res)
        }).catch(err => {
          reject(err)
        })
    })
  },
  //用户未传递参数使用第二个对象属性，只传递了content使用第一个参数
  //添加笔记再返回一个Promise对象
  addNote({ notebookId },  { title = '', content = ''} = { title: '', content: ''}) {
    return new Promise((resolve, reject) => {
      request(URL.ADD.replace(':notebookId', notebookId), 'POST', { title, content })
        .then(res => {
          res.data.createdAtFriendly = friendlyDate(res.data.createdAt)
          res.data.updatedAtFriendly = friendlyDate(res.data.updatedAt)
          resolve(res)
        }).catch(err => {
          reject(err)
        })
    })
  },

  //更新笔记标题,传递哪个用哪个属性
  updateNote({ noteId }, { title, content}) {
    console.log(noteId, title, content)
    return request(URL.UPDATE.replace(':noteId', noteId), 'PATCH', { title, content })
  },

  deleteNote({ noteId }) {
    return request(URL.DELETE.replace(':noteId', noteId), 'DELETE')
  }
}


