<template>
  <span :title="username">{{slug}}</span>
</template>

<script>

  //引入Getter, Actions
  import { mapGetters, mapActions } from 'vuex'

  export default {
    data() {
      return {
        //删除数据
      }
    },

    created() {
      //调用验证用户是否登录的方法
      this.setUser()
    },

    methods: {
      //添加验证用户是否登录的方法,换个对象名字setUser
      ...mapActions({
        'setUser': 'checkLogin'
      })
    },

    computed: {
      //引入Getter
      ...mapGetters([
        'username',
        'slug'
      ])
    }
  }
</script>

<style scoped>
  span {
    display: inline-block;
    width: 30px;
    height: 30px;
    text-align: center;
    line-height: 32px;
    border-radius: 50%;
    background: #f2b81c;
    color: #fff;
    text-shadow: 1px 0 1px #795c19;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 18px;
    margin-top: 15px;
  }
</style>


