import Note from '@/apis/notes'
//引入Message
import { Message } from 'element-ui'

const state = {
  //添加curBookId,设置notebooks为空
  notes: null,
  curNoteId: null
}

const getters = {
  //获取curNote
  notes: state => state.notes || [],
  curNote: state => {
    //若不是数组，返回空，若不存在，返回第一个
    //报错修复添加一个空对象
    //4.设置当前笔记标题和内容为空
    if(!Array.isArray(state.notes)) return { title: '', content: '' }
    if(!state.curNoteId) return state.notes[0] || { title: '', content: '' }
    //即是数组又存在,找到curNote
    return state.notes.find(note => String(note.id) === String(state.curNoteId)) || { title: '', content: '' }
  }
}

//封装异步操作数据
const mutations = {
  setNotes(state, payload) {
    state.notes = payload.notes
  },

  addNote(state, payload) {
    state.notes.unshift(payload.note)
  },

  updateNote(state, payload) {
    let notebook = state.notes.find(note => note.id === payload.noteId) || {}
    note.title = payload.title
    note.content = payload.content
  },

  deleteNote(state, payload) {
    state.notes = state.notes.filter(note => note.id !== payload.noteId)
  },
  //设置setCurNote
  //3.添加payload为空
  setCurNote(state, payload = {}) {
    state.curNoteId = payload.curNoteId
  }
}

//异步服务器交互，同步修改mutations的数据
//添加return，返回的是Promise对象，可以继续用then
const actions = {
  getNotes({ commit }, {notebookId}) {
    return Note.getAll({notebookId})
      .then(res => {
        //修改数据
        commit('setNotes', { notes: res.data })
      })
  },
  //添加、更新、删除数据
  addNote({ commit }, { notebookId, title, content }) {
    return Note.addNote({notebookId}, { title, content })
      .then(res => {
        commit('addNote', { note: res.data })
      })
  },

  updateNote( {commit}, { noteId, title, content } ) {
    return Note.updateNote({noteId }, { title, content })
      .then(res => {
        commit('updateNote', { noteId, title, content })
      })
  },

  deleteNote( {commit}, { noteId } ) {
    return Note.deleteNote( {noteId} )
      .then(res => {
        commit('deleteNote', { noteId })
        //添加Message
        Message.success(res.msg)
      })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}


