//导入auth接口
import Auth from '@/apis/auth'
//导入router
import router from '@/router'

//测试路由跳转
window.router = router
const state = {
  user: null
}

const getters = {
  username: state => state.user === null ? '未登录' : state.user.username,

  slug: state => state.user === null ? '未' : state.user.username[0]
}

const mutations = {
  //提交commit，setUser传递user属性，修改getters里的信息
  setUser(state, payload) {
    state.user = payload.user
  }
}

const actions = {
  //加入login和register方法,提交信息时，payload相当于user,里面的信息为res.data
  login({ commit }, { username, password }) {
    return Auth.login({ username, password })
      .then(res => {
        commit('setUser', { user: res.data })
      })
  },
  register({ commit }, { username, password }) {
    return Auth.register({ username, password })
      .then(res => {
        commit('setUser', { user: res.data })
      })
  },

  //10.注销时添加logot方法
  logout({ commit }, payload = { path: '/login' }) {
    return Auth.logout()
      .then(res => {
        commit('setUser', { user: null })
        //12.注销之后跳转到登录页面     
        router.push(payload)
      })
  },

  //添加验证用户是否登录的方法,payLoad作为检测用户是否登录的参数
  checkLogin({ commit, state }, payload) {
    //回收站用户优化
    if(state.user !== null) return Promise.resolve()
    return Auth.getInfo()
      .then(res => {
        if(!res.isLogin) {
          //添加跳转到首页的路由
          router.push(payload)
        } else {
          commit('setUser', { user: res.data })
        }
      })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}


